import React, { useState } from "react";
import classes from "./Item.module.scss";
import { motion } from "framer-motion";
import PlayIcon from "../../images/icon-play.svg";
import Movie from "../../images/icon-category-movie.svg";
import useAuth from "../../contexts/AuthContext";
import useBookmarksCtx from "../../contexts/BookmarksContext";
import { useNavigate } from "react-router-dom";

function Item(props) {
  const { currentUser } = useAuth();
  const { addBookmark, checkInBookmarks, removeBookmark } = useBookmarksCtx();
  const navigate = useNavigate();
  const { id: itemId } = props.data;
  const alreadyBookmarked = checkInBookmarks(itemId);
  const [bookmarked, setBookmarked] = useState(alreadyBookmarked);
  // props?.initial || false

  const {
    title,
    rating,
    category,
    id,
    thumbnail: {
      regular: { medium: mediumImage, large: largeImage },
    },
    year,
  } = props.data;

  const { isTrend = false } = props;

  let image = isTrend ? largeImage : mediumImage;

  let detailsPathName = category.replace(/ /gi, "").toLowerCase();
  //! edit
  if (detailsPathName === "movie") detailsPathName = "movies";
  function itemClickHandler(e) {
    console.log("Navigate");
    navigate(`/${detailsPathName}/${id}`);
  }

  function bookmarkClickHandler(e) {
    if (!currentUser) return;

    if (!bookmarked) {
      addBookmark(props.data);
    } else {
      removeBookmark(id);
    }

    setBookmarked((prev) => !prev);
  }
  // console.log(bookmarked);
  return (
    <motion.div
      animate={{ opacity: 1 }}
      initial={{ opacity: 0 }}
      exit={{ opacity: 0 }}
      layout
      className={classes.item}
    >
      <div className={classes["item__image"]}>
        <span
          className={classes["item__bookmark"]}
          onClick={bookmarkClickHandler}
        >
          <ion-icon name={`bookmark${bookmarked ? "" : "-outline"}`}></ion-icon>
        </span>

        <span
          className={classes["item__image__overlay"]}
          onClick={itemClickHandler}
        >
          <span className={classes["overlay__content"]}>
            {" "}
            <img src={PlayIcon} alt="" /> Play{" "}
          </span>
        </span>
        <img src={image} alt="icon" />
      </div>

      <div className={isTrend ? classes["trend_item_description_holder"] : ""}>
        <div className={classes["item__details"]}>
          <span className="item__year">{year}</span>
          <span className="item__category">
            <img src={Movie} alt="item" /> {category}
          </span>
          <span className="item__class">{rating}</span>
        </div>
        <p
          className={classes[isTrend ? "item__name__big" : "item__name__small"]}
        >
          {title}
        </p>
      </div>
    </motion.div>
  );
}

export default Item;
