import React from "react";
import NotFoundGif from "../../images/not-found.gif";

const styles = {
  wrapper: {
    display: "flex",
    justifyContent: "center",
    flexDirection: "column",
    alignItems: "center",
    padding: "70px 0",
  },
  title: {
    fontSize: "1.7rem",
    color: "#fff",
    textAlign: "center",
    marginBottom: "20px",
  },
  link: { color: "white", marginTop: "7px" },
};

function NotFound() {
  return (
    <div style={styles.wrapper}>
      <p style={styles.title}>Page Not Found 404</p>
      <img src={NotFoundGif} alt="Not Found" />
      <span>
        <a style={styles.link} href="https://giphy.com/gifs/404-14uQ3cOFteDaU">
          via GIPHY
        </a>
      </span>
    </div>
  );
}

export default NotFound;
