import React, { useEffect, useState } from "react";
import useStorage from "../../../hooks/useStorage";

const styles = {
  progress: {
    width: "100%",
    display: "grid",
    placeItems: "center",
    height: "100%",
    color: "#fff",
    fontSize: "24px",
    position: "absolute",
    top: 0,
    left: 0,
    borderRadius: "50%",
    background: "#ffffff1c",
  },
};

function Progress({ file, handleError, finishHandler, toggleLoading }) {
  const [url, progress, error] = useStorage(file);

  if (url) {
    finishHandler(url);
  }

  if (error) {
    handleError(error);
  }

  useEffect(() => {
    let timeout;
    if (!url) {
      timeout = setTimeout(() => {
        console.log("still loading");
        toggleLoading(true);
      }, 500);
    } else {
      toggleLoading(false);
    }

    return () => {
      clearTimeout(timeout);
    };
  }, [progress, toggleLoading, url]);

  return (
    <>
      {!error && progress !== 100 && (
        <span style={styles["progress"]}>{progress}</span>
      )}
    </>
  );
}

export default Progress;
