import React, { useReducer } from "react";
import filmsData from "../data/data.json";

function contextReducer(state, action) {
  switch (action.type) {
    case "UPDATE_SEARCH": {
      return {
        ...state,
        searchTerm: action.value,
        filteredMovies: filmsData.filter((film) =>
          film.title.toLowerCase().includes(action.value.toLowerCase())
        ),
      };
    }

    default: {
      return state;
    }
  }
}

function getTrending(allFilms) {
  return allFilms.filter((film) => film.isTrending);
}
function getMovies(allFilms) {
  return allFilms.filter((film) => film.category === "Movie");
}
function getTvSeries(allFilms) {
  return allFilms.filter((film) => film.category === "TV Series");
}

const FilmsCtx = React.createContext();
const defaultState = {
  filteredMovies: filmsData,
  searchTerm: "",
};

function FilmContextProvider(props) {
  const [filmsState, dispatch] = useReducer(contextReducer, defaultState);
  const { filteredMovies, searchTerm } = filmsState;

  let movies = getMovies(filteredMovies);
  let tvSeries = getTvSeries(filteredMovies);
  let trending = getTrending(filteredMovies);
  let isSearching = searchTerm.trim() !== "";

  const contextValue = {
    films: filteredMovies,
    movies,
    tvSeries,
    trending,
    isSearching,
    searchTerm,

    updateSearch(term) {
      dispatch({ type: "UPDATE_SEARCH", value: term });
    },
  };

  return (
    <FilmsCtx.Provider value={contextValue}>{props.children}</FilmsCtx.Provider>
  );
}

export { FilmContextProvider };

export default FilmsCtx;

