import React from "react";
import { useParams } from "react-router-dom";
import useData from "../../hooks/useData";
import classes from "./ItemDetails.module.scss";
import NotFound from "../Routing/NotFound";

function ItemDetails() {
  const { id } = useParams();
  const { films } = useData();
  let matchedItem = films.find((film) => film.id === +id);

  if (!matchedItem) {
    console.log("no films found");
    return <NotFound />;
  }

  let {
    title,
    rating,
    category,
    thumbnail: {
      regular: { large: largeImage },
    },
    year,
  } = matchedItem;
  largeImage = '../' + largeImage

  //! edit image
  return (
    <div className={classes.container}>
      <div className={classes.image}>
        <img src={largeImage} alt="Item" />
      </div>

      <div className={classes.details}>
        <h2>{title}</h2>
        <div className={classes["inner-flex"]}>
          <p className={classes.category}>{category}</p>
          <p className={classes.year}>{year}</p>
          <p className={classes.rating}>{rating}</p>
        </div>

        <p className={classes["description"]}>
          Lorem ipsum dolor sit amet consectetur adipisicing elit. Maxime
          mollitia, molestiae quas vel sint commodi repudiandae consequuntur
          voluptatum laborum numquam blanditiis harum quisquam eius sed odit
          fugiat iusto fuga praesentium optio, eaque rerum! Provident similique
          accusantium nemo autem. Veritatis obcaecati tenetur iure eius earum ut
          molestias architecto voluptate aliquam nihil, eveniet aliquid culpa
          officia aut! Impedit sit sunt quaerat, odit, tenetur error, harum
          nesciunt ipsum debitis quas aliquid. Reprehenderit, quia. Quo neque
          error repudiandae fuga? Ipsa laudantium molestias eos sapiente olorem!
          Officiis iure rerum voluptates a
        </p>
      </div>
    </div>
  );
}

export default ItemDetails;
