import React, { useContext } from "react";
import classes from "./SearchBar.module.scss";
import FilmsCtx from "../../contexts/filmsContext";
import { useLocation } from "react-router-dom";

function SearchBar() {
  const { updateSearch } = useContext(FilmsCtx);

  const location = useLocation();
  const removedInPathes = /(login|signup|user)/gi.test(location.pathname);

  if (removedInPathes) return null;

  function inputHandler(e) {
    updateSearch(e.target.value);
  }

  return (
    <form htmlFor="searchInput" className={classes.label}>
      <ion-icon name="search"></ion-icon>
      <input
        type="text"
        placeholder="Search for movies or TV series"
        onInput={inputHandler}
      />
    </form>
  );
}

export default SearchBar;
