import React from "react";

import Item from "../../Item/Item";
import ItemGrid from "../../UI/itemsGrid/ItemsGrid";
import Message from "../../UI/Message/Message";

function MoviesBookmarks({ moviesBookmarks, styling }) {
  const bookmarksElements = moviesBookmarks.map((bm, index) => (
    <Item data={bm} key={index} />
  ));

  return (
    <div>
      <h2 style={styling.title}>Movies Bookmarks</h2>
      {bookmarksElements.length !== 0 ? (
        <ItemGrid>{bookmarksElements}</ItemGrid>
      ) : (
        <Message message="No Bookmarks Were Found" />
      )}
    </div>
  );
}

export default MoviesBookmarks;
