import React, { useEffect, useState } from "react";
import { doc, getDoc, onSnapshot } from "firebase/firestore";
import { db } from "../firebase/firebase";

function useBookmarks(user) {
  const { uid: userId = null } = user || {};
  const [bookmarks, setBookmarks] = useState([]);
  const [hasError, setError] = useState(null);

  useEffect(() => {
    if (!userId) {
      setError("Sign in to access bookmarks");
      return;
    }

    const docSnap = doc(db, "users", userId);
    const unsub = onSnapshot(docSnap, (doc) => {
      setError(false);

      if (doc?.exists()) {
        setBookmarks(doc.data().bookmarks);
      } else {
        setError("Cannot Get Bookmarks");
      }
    });

    return unsub;
  }, [user, userId]);
  return [bookmarks, hasError];
}

export default useBookmarks;
