import React, { useRef, useState } from "react";
import {} from "firebase/storage";
import classes from "./Profile.module.scss";
import FormButtons from "./FormButtons";
import useUserDetails from "../../../hooks/useUserDetails";
import ProfileImage from "./ProfileImage";
import useAuth from "../../../contexts/AuthContext";
import { doc, setDoc, updateDoc } from "firebase/firestore";
import { db } from "../../../firebase/firebase";

function Profile() {
  const { currentUser, updateUser } = useAuth();
  const [isEditing, setIsEditing] = useState(false);
  const [message, setMessage] = useState(null);
  const [userDetails, userDetailsError] = useUserDetails(currentUser);
  const nameInput = useRef();
  const titleInput = useRef();
  const bioInput = useRef();

  const toggleEditing = () => {
    setIsEditing((prev) => !prev);
  };

  let formStyles = `${classes["flex"]} ${classes["flex-left"]} ${
    isEditing ? classes["editing"] : ""
  }`;

  async function submitHandler(e) {
    setMessage(null);
    e.preventDefault();
    const [name, title, bio] = [
      nameInput.current.value,
      titleInput.current.value,
      bioInput.current.value,
    ];

    if (!name || !title || !bio) {
      setMessage({ text: "Inputs cannot be empty", status: "error" });
      setIsEditing(false);
      return;
    }

    const modifiedDetails = {
      ...userDetails.details,
      title,
      bio,
    };

    const modifiedCred = {
      ...userDetails.userCred,
      name: name,
    };

    try {
      await updateDoc(doc(db, "users", currentUser.uid), {
        details: modifiedDetails,
        userCred: modifiedCred,
      });

      await updateUser({ displayName: name });

      setMessage({ text: "Changes made successfully", status: "success" });
    } catch (e) {
      setMessage({
        text: e.message || "Cannot modify changes at the moment",
        status: "error",
      });
    }
  }

  return (
    <div className={classes.profile}>
      <form className={formStyles} onSubmit={submitHandler}>
        {userDetailsError && <p>{userDetailsError}</p>}

        <ProfileImage classes={classes} />

        <input
          ref={nameInput}
          type="text"
          defaultValue={userDetails?.userCred?.name}
          placeholder="Enter your name"
          className={classes["name-input"]}
        />

        <input
          ref={titleInput}
          className={classes["title"]}
          defaultValue={userDetails?.details?.title}
          placeholder="Enter a title"
        />

        <textarea
          ref={bioInput}
          className={classes["bio"]}
          defaultValue={userDetails?.details?.bio}
          placeholder="Add a bio"
        ></textarea>

        {message && <p className={classes[message.status]}>{message.text}</p>}

        <FormButtons isEditing={isEditing} toggleEditing={toggleEditing} />
      </form>
    </div>
  );
}

export default Profile;
