import React, { useContext } from "react";
import useAuth from "./AuthContext";
import { db } from "../firebase/firebase";
import useBookmarks from "../hooks/useBookmarks";
import {
  doc,
  updateDoc,
  arrayUnion,
  arrayRemove,
  FieldValue,
} from "firebase/firestore";

export default function useBookmarksCtx() {
  return useContext(BookmarksContext);
}
let BookmarksContext = React.createContext();

function BookmarksContextProvider({ children }) {
  const { currentUser = null } = useAuth() || {};

  let [bookmarks, bookmarksError] = useBookmarks(currentUser);

  // error in exporting
  function checkInBookmarks(itemId) {
    const isFound = bookmarks.find((bm) => bm.id === itemId);
    return isFound ? true : false;
  }

  async function addBookmark(item) {
    let userDocRef = doc(db, "users", currentUser.uid);
    try {
      updateDoc(userDocRef, { bookmarks: arrayUnion(item) });
    } catch {
      console.log("error while adding a new bookmark");
    }
  }

  function removeBookmark(itemId) {
    let userDocRef = doc(db, "users", currentUser.uid);

    try {
      updateDoc(userDocRef, {
        bookmarks: bookmarks.filter((bm) => bm.id !== itemId),
      });
    } catch {
      console.log("error while removing a bookmark");
    }
  }

  let contextValue = {
    addBookmark,
    bookmarks,
    bookmarksError,
    checkInBookmarks,
    removeBookmark,
  };

  return (
    <BookmarksContext.Provider value={contextValue}>
      {children}
    </BookmarksContext.Provider>
  );
}

export { BookmarksContextProvider };

