import React from "react";

const styles = {
  message: {
    fontSize: "clamp(1.3rem, 5vmin, 1.6rem)",
    textAlign: "center",
    margin: "15px",
    color: "#fff",
  },
};

function Message({ message }) {
  return <p style={styles.message}>{message}</p>;
}

export default Message;
