import React from "react";
import "./normalize.css";
import Navbar from "./components/Navbar/Navbar";
import SearchBar from "./components/SearchBar/SearchBar";
import classes from "./App.module.scss";
import Routing from "./components/Routing/Routing";
import useData from "./hooks/useData";
import FrontendMentor from "./images/favicon.png";

const styles = {
  searchTracker: {
    marginTop: "10px",
    fontSize: "clamp(1.1rem, 7vmin, 1.4rem)",
    color: "#fff",
  },
};

function App() {
  const { films, searchTerm, isSearching } = useData();
  const searchText = `Found ${films.length} results for '${searchTerm}'`;

  return (
    <>
      <div className={`${classes.wrapper} container`}>
        <div className={classes["flex-left"]}>
          <Navbar />
        </div>

        <div className={classes["flex-right"]}>
          <SearchBar />
          {isSearching && <p style={styles.searchTracker}>{searchText}</p>}

          <Routing />
        </div>
      </div>

      <a
        className={classes["frontend-mentor"]}
        href="https://www.frontendmentor.io/challenges/entertainment-web-app-J-UhgAW1X"
        target="_blank"
        rel="noreferrer"
      >
        <img src={FrontendMentor} alt="Frontend Mentor" />
        Frontend Mentor
      </a>
    </>
  );
}

export default App;
