import React, { useEffect, useState } from "react";
import { onSnapshot, doc } from "firebase/firestore";
import { db } from "../firebase/firebase";

function useUserDetails(user) {
  const [userDetails, setUserDetails] = useState(null);
  const [error, setError] = useState(null);

  useEffect(() => {
    const { uid: userId = null } = user || {};

    const docSnap = doc(db, "users", userId);
    const unsub = onSnapshot(docSnap, (doc) => {
      setError(false);

      if (doc?.exists()) {
        let userCred = doc.data()?.userCred;
        let details = doc.data()?.details;

        setUserDetails({ userCred, details });
      } else {
        setError("Cannot Get User Details");
      }
    });

    return unsub;
  }, [user]);

  return [userDetails, error];
}

export default useUserDetails;
