import React from "react";
import classes from "./FormButtons.module.scss";
const { buttonStyles } = classes;

function FormButtons({ isEditing, toggleEditing }) {
  return (
    <div>
      {!isEditing ? (
        <button onClick={toggleEditing} className={buttonStyles}>
          Edit
        </button>
      ) : (
        <div className={classes["btns-holder"]}>
          <button onClick={toggleEditing} className={buttonStyles}>
            Cancel
          </button>
          <button type="submit" className={buttonStyles}>
            Save
          </button>
        </div>
      )}
    </div>
  );
}

export default FormButtons;
