import React, { useState, useContext, useEffect } from "react";
import { auth, db } from "../firebase/firebase";
import {
  signInWithEmailAndPassword,
  createUserWithEmailAndPassword,
  onAuthStateChanged,
  signOut,
  updateProfile,
} from "firebase/auth";
import { doc, setDoc } from "firebase/firestore";

//* the data that will be with every user
const USER_STORED_DATA = {
  details: {
    plan: null,
    title: null,
    bio: null,
    profileImage: null,
  },
  bookmarks: [],
};

export default function useAuth() {
  return useContext(AuthContext);
}

const AuthContext = React.createContext();

function AuthContextProvider({ children }) {
  const [user, setUser] = useState();

  function signup(name, email, password) {
    return createUserWithEmailAndPassword(auth, email, password).then(
      (userCred) => {
        updateProfile(auth.currentUser, {
          displayName: name,
        });
        const user = userCred.user;
        const userCredentials = { name, email };
        storeUserData(user.uid, userCredentials);
      }
    );
  }

  async function storeUserData(id, userCred) {
    const userData = {
      ...USER_STORED_DATA,
      userCred,
    };

    setDoc(doc(db, "users", id), userData);
  }

  function signin(email, password) {
    return signInWithEmailAndPassword(auth, email, password);
  }

  function signout() {
    signOut(auth);
  }

  function updateUser(data) {
    updateProfile(user, {
      ...data,
    });
  }

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (user) => {
      if (user) {
        setUser(user);
      } else {
        setUser();
      }
    });

    return unsubscribe;
  }, []);

  let value = {
    currentUser: user,
    updateUser,
    signin,
    signup,
    signout,
  };

  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
}

export { AuthContextProvider };
