import React from "react";
import MoviesBookmarks from "./MoviesBookmarks";
import SeriesBookmarks from "./SeriesBookmarks";
import useBookmarksCtx from "../../../contexts/BookmarksContext";
import Message from "../../UI/Message/Message";

const styles = {
  title: {
    fontSize: "clamp(1.4rem, 7vmin, 2rem)",
    color: "#fff",
    margin: "25px 0 25px",
    fontWeight: "500",
  },
};

function Bookmarks() {
  const { bookmarksError, bookmarks } = useBookmarksCtx();

  const seriesBookmarks = bookmarks.filter(
    (bookmark) => bookmark.category === "TV Series"
  );

  const moviesBookmarks = bookmarks.filter(
    (bookmark) => bookmark.category === "Movie"
  );

  if (bookmarksError || bookmarks.length === 0) {
    return <Message message={bookmarksError || "No Bookmaks Were Added"} />;
  }

  return (
    <div>
      <MoviesBookmarks styling={styles} moviesBookmarks={moviesBookmarks} />
      <SeriesBookmarks styling={styles} seriesBookmarks={seriesBookmarks} />
    </div>
  );
}

export default Bookmarks;
